import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { GlobalVarsProvider } from '../../providers/global-vars/global-vars';
import { AlertServiceProvider } from '../../providers/alert-service/alert-service';
import { ClientdataProvider } from '../../providers/clientdata/clientdata';
import { ContactsProvider } from '../../providers/contacts/contacts';
import { airtime } from '../../menus';
@IonicPage()
@Component({
  selector: 'page-topup',
  templateUrl: 'topup.html',
})
export class TopupPage {
  utilities: any = [];
  langs: any;
  constructor( public contactsProvider: ContactsProvider, public alertService: AlertServiceProvider, private clientdata: ClientdataProvider,public navCtrl: NavController,public globalVars: GlobalVarsProvider, public navParams: NavParams) {
    this.utilities = airtime;
    this.langs = this.globalVars.doTranslate( this.globalVars.getLang() ); 
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad TopupPage');
  }
  goHome(){
    this.navCtrl.setRoot('HomePage');
  }
  ionViewDidEnter() {
    this.globalVars.reload();

  }
  openPage( mypage ) {
     this.navCtrl.push( 'AirtimePage', {
			data: mypage
		} );
  }

  getItems( ev: any ) {

    console.log("because here");
    this.utilities;
    let val = ev.target.value;
    if ( val && val.trim() != '' ) {
      this.utilities = this.utilities.filter(( utility ) => {
        return ( utility.title.toLowerCase().indexOf( val.toLowerCase() ) > -1 );
      } )
    }
    else {
      this.utilities = this.globalVars.allData.topup;
    }
  }  

  back() {
    this.navCtrl.pop();
  }


}



